@charset "utf-8";
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,rem,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,rembed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{border:0;font-size:100%;vertical-align:baseline;outline:none;box-sizing:border-box;margin:0;padding:0;font-weight: normal;}
html{height:100%;overflow-y:auto; -webkit-text-size-adjust:100%;-ms-text-size-adjust: 100%;-webkit-font-smoothing:antialiased;-webkit-tap-highlight-color:rgba(0,0,0,0);font-size:62.5%;}
body{text-align:center;line-height:1;font-family:Helvetica,"Microsoft YaHei",sans-serif;width: 100%;overflow-x: hidden;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:-moz-none;-ms-user-select:none;user-select:none;-webkit-overflow-scrolling:touch;}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}
ol,ul{list-style:none}
blockquote,q{quotes:none}
i{font-style: normal;}
blockquote:before,blockquote:after,q:before,q:after{content:none}
table{border-collapse:collapse;border-spacing:0}
img{max-width:100%;width: 100%;border:0;vertical-align:middle;-ms-interpolation-mode:bicubic}
a{color: #333;text-decoration: none;}a:active {color: #0062cc;}
:focus{outline:none;-webkit-tap-highlight-color:rgba(0,0,0,0);-moz-tap-highlight-color:rgba(0,0,0,0);tap-highlight-color:rgba(0,0,0,0)}
input,button,textarea{vertical-align:middle;border: 1px solid #95b8e7;-webkit-appearance: none; font-family: Helvetica,"Microsoft YaHei",sans-serif;}
.clearfix:after,.clearfix:before{display:table;content:""}
.clearfix:after{clear:both;overflow:hidden}
@font-face {font-family: iconfont;font-style: normal;font-weight: normal;src: url('iconfont.woff') format('woff'),url("iconfont.ttf") format("truetype");}
.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

header{ background-color: #2faee5; color: #fff; height: 1rem; position: relative;}
header #menu-a{ font-size: 0.4rem; left: .36rem;top: .34rem; position: absolute; display: block;cursor: pointer;}
header h1{ font-size: .38rem; line-height: 1rem; height: 1rem; margin: 0; padding: 0 .8rem}

.pd06 {padding-top: 0.6rem;}
.pd02 {padding: 0.2rem;}
.main_wrap{ background-color: #cdf0ff;padding: 0.3rem 0.2rem 1.5rem;}
.main_header{height:0.6rem;width: 100%; background:url("../images/header_bg.png") center center no-repeat; background-size: auto 0.6rem; position: absolute; top:0; left:0; z-index: 9;}
.main_header>h1{font-size: 0.38rem; line-height: 0.7rem;}
.main_con{width: 100%; padding-bottom:0.6rem; background-color: #fff; position: relative; box-shadow: 0 0 10px rgba(0,0,0,0.2); border-radius: 0.1rem;}
.main_con .bottom_bg{width:100%; height:0.24rem; background:url("../images/main_bg.png") center top no-repeat; background-size: auto 0.24rem; position: absolute; bottom:-0.12rem; left:0;}
.main_con>.chMsg{padding:0.2rem 0.2rem 0 0.2rem;}
.main_con>.chMsg>li{line-height: 0.5rem; text-align: left;}
.main_con>.chMsg>li>span,.main_con>ul>li>p{color:#333; font-size: 0.3rem; line-height: 0.5rem; }
.main_con>.chMsg>li>span{display: inline-block; }
.main_con>.chMsg>li>p{padding-left: 0.6rem;}
.main_con>.chMsg>li>span.span_r{color:#2faee5; margin-right: 0.4rem;  background-size: auto 0.4rem; padding-left: 0.6rem;}
.main_con>.chMsg>li:nth-child(2)>span.span_r{ background-size: auto 0.4rem;}
.main_con>.chMsg>li:nth-child(4)>span.span_r{background-size: auto 0.4rem; margin-bottom: 0.2rem;}

.main_con_txt {
    color: #666;
    margin: 0 auto;
    padding-top: 0.3rem;
    width: 90%;
}

.main_con_txt p {
    font-size: 0.3rem;
    line-height: 0.5rem;
    text-align: left;
    text-indent: 2em;
}
footer{bottom: 0;left: 0;position: fixed; height: 1rem; background: #eee; color: #666; width: 100%;}
footer li{ width: 25%;float: left; font-size: .26rem;}
.footer li span{display: block; width: 100%; line-height: .6rem; font-size: .4rem;}


