







































































  
.wyhz_others {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    margin-top: 0.6rem;
}

.hy_con_item {
    background-color: #fff;
    padding-top: 0.24rem;
    position: relative;
}

.hy_media {
    margin-bottom: 0.2rem;
    padding: 0.24rem;
    width: 100%;
}
.hy_media .titl {
    border-top: 1px solid #000;
    margin: 0.4rem auto 0;
    position: relative;
    width: 100%;
}
.hy_media .titl > span {
    background-color: #fff;
    font-size: 0.24rem;
    left: 1.8rem;
    position: absolute;
    top: -0.1rem;
}
.hy_media > ul {
    float: left;
    margin-top: 0.3rem;
}
.hy_media > ul > li {
    font-size: 0.24rem;
    line-height: 0.4rem;
    text-align: left;
}
.hy_media > p {
    height: 0.5rem;
    position: relative;
    text-align: left;
}
.hy_media > p strong {
    font-size: 0.28rem;
    left: 0;
    position: absolute;
    top: 0;
    width: 2.2rem;
}
.hy_media > p img {
    height: 0.32rem;
    left: 2.3rem;
    position: absolute;
    top: 0rem;
    width: auto;
}
.hy_media > h3 {
    font-size: 0.28rem;
    font-weight: bold;
    text-align: left;
}
.hy_media .dsxb {
    left: -2.2rem;
    position: relative;
    top: 0.1rem;
    width: 0.8rem;
}
.hy_media .wzxb {
    margin-top: 0.4rem;
    width: 5.8rem;
}



