
























  
.news_list {
    color: #666;
    margin: 0 auto;
    padding-top: 0.3rem;
    width: 94%;
}
.news_list li {
    height: 1.4rem;
    list-style: outside none none;
}
.news_list li a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.news_list li img {
    height: 1.2rem;
    width: 1.6rem;
}.news_list .hy_box_words {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 70%;
            flex: 0 0 70%;
    height: 1.2rem;
    margin-left: 0.18rem;
    overflow: hidden;
    position: relative;
    text-align: left;
}.news_list .hy_box_words p {
    -moz-box-orient: vertical;
    color: #000;
    display: -webkit-box;
    font-size: 0.28rem;
    height: 0.8rem;
    line-height: 0.4rem;
    overflow: hidden;
    width: 100%;
}
.news_list .hy_box_words span {
    bottom: 0.1rem;
    color: #aaa;
    font-size: 0.2rem;
    left: 0;
    position: absolute;
}



