nav{background:#fff none repeat scroll 0 0;height:100%;left:0;overflow:hidden;position:fixed;width:100%;z-index:0;opacity:0;-webkit-transition:all .3s ease-in;transition:all .3s ease-in}nav ul{width:42%;height:100%;box-shadow:-10px -2px 20px #ddd inset}nav ul li{text-align:center;border-bottom:1px solid #eee}nav a{color:#666;font-size:.30rem;display:block;height:.8rem;line-height:.8rem}nav.show{opacity:1}



